@import url("//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/bttn.css/0.2.4/bttn.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/bttn.css/0.2.4/standalone/material-flat.min.css");
@import url("https://fonts.googleapis.com/css?family=Open+Sans") * {
  margin: 0;
  padding: 0;
}

#main-wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #000;
  font-family: "Open Sans", sans-serif;
  color: white;
  align-content: center;
  min-width: 300px;
  min-height: 400px;
}

#quote-box {
  width: 33.3333333333vw;
  height: auto;
  background-color: #fff;
  border-radius: 3px;
  padding: 2.5vw 5vw;
}

#buttons-area {
  display: flex;
  justify-content: space-between;
  margin-top: 2.5vw;
}

#author {
  margin-top: 10px;
  text-align: right;
  font-size: 16px;
}

#text {
  font-size: 32px;
}

#quote {
  padding-right: 10px;
}

@media only screen and (max-width: 1028px) {
  #quote-box {
    width: 50vw;
    min-width: 300px;
    padding: 5vw;
  }

  #text {
    font-size: 24px;
  }
}
